<template>
	<div class="recharge">
		<van-nav-bar
		  fixed
		  border
		  title="充值中心"
		  left-arrow
		  @click-left="goBack"
		/>
		<div class="recharge_cent">
			<div class="recharge_xz">
				<div v-for="(item,index) in arr" :key="index" @click="choice(item,index)" :class="{active:currIndex==index}">
					<p class="num" :class="{cor:currIndex==index}">{{item.title}}</p>
					<p class="moeny" :class="{cor:currIndex==index}">￥{{item.price}}</p>
					<p class="title">{{item.tag}}</p>
					<!-- <van-tag round type="danger" v-if="item.tag" size="large">{{item.tag}}</van-tag> -->
				</div>
			</div>
			<!-- <div class="vip_bg"></div> -->
		</div>
		<van-cell-group class="celltop" v-if="showType == 'integral'">
			<van-cell class="tips_title" title="温馨提示">
			<template #right-icon>
			    <span class="right" @click="showTips">查看积分说明<i class="iconfont iconyou"></i></span>
			  </template>
			</van-cell>  
			<div class="tips">
				<p>1.充值购买积分仅限于本人天集社平台内使用</p>
				<p>2.充值购买的积分不过期、不清零、长期有效</p>
				<p>3.积分充值比例为1元兑换10个积分</p>
				<p>4.积分最终解释权归天集社所有</p>
			</div>
		</van-cell-group>
		<van-cell-group class="celltop" v-if="showType == 'vip'">
			<van-cell class="tips_title" title="天集社包年会员权益"/>
			<div class="tips">
				<p>1.一年不限量免费对接天集社平台需求</p>
				<p>2.一年不限量名片交换</p>
				<p>3.客服人员一对一服务</p>
				<p>4.优先联系需求方，对接相关需求</p>
				<p>5.相关优质需求，精准推送对接</p>
				<p>6.发布信息时，视质量可做全平台推送</p>
				<p>7.身份特权，尊享VIP会员头衔</p>
			</div>
		</van-cell-group>
		<van-button type="warning" size="large" block @click="creatOd" >立即充值</van-button>
	</div>
</template>

<script>
import {Col, Row,NavBar,Cell, CellGroup,Button,Tag,Grid,GridItem} from "vant";	
import {recharge,createOrder,generateOrderSn} from "@/api/v4.js"
export default{
	components:{
		[NavBar.name]: NavBar,
		[Col.name]: Col,
		[Row.name]: Row,
		[Cell.name]: Cell,
		[CellGroup.name]: CellGroup,
		[Button.name]: Button,
		[Tag.name]: Tag,
		[Grid.name]: Grid,
		[GridItem.name]: GridItem,
		 
	},
	data(){
		return{
			currIndex:null,
			arr:[],
			odId:null,
			price:null,
			type:null,
			info:[],
			showID:2,
			showType:"integral"
		}
	},
	created(){
		recharge().then(res=>{
			this.arr = res
		})
	},
	methods:{
		showTips() {
			this.$dialog.alert({
				title: "积分使用说明（20200402）",
				messageAlign:"left",
				message: "<strong>1.积分消耗:</strong>对接需求,可以使用积分支付,消耗10积分;\n<strong>2.名片交换</strong>,可以使用积分支付,消耗5积分\n<strong>3.积分增加:</strong>\na.新注册天集社用户,赠送50积分; \nb.个人中心名片认证申请通过, 赠送50积分; \nc.公司认证审核通过, 赠送100积分; \nd.成功发布一个需求, 赠送20积分; \ne.通过邀请海报分享, 每成功邀请一名新用户注册可获赠50积分;\nf.邀请注册的好友充值限时获得10 % 比例积分奖励;\ng.充值可获得相应积分。",
			})
		},
		goBack() {
			this.$router.back();
		},
		choice(item,index){
			if (index != this.currIndex) {
				this.currIndex = index
				this.odId = item.id
				this.showID = item.id
				this.price = item.price
				this.type = item.type
				this.info = item
				this.showType = item.type
			} else{
				this.currIndex = null
			}
		},
		creatOd(){
			if(this.currIndex == null){
				this.$dialog.alert({
					title:"温馨提示",
					message:"请选择充值商品"
				})
			}else{
				generateOrderSn().then(res=>{
					if(res.order_sn){
						this.$router.push({path: '/integral/buy', query: {odId: this.odId,type:this.type,price:this.price,order_sn:res.order_sn}})
					}else{
						this.$dialog.alert({
							message:"创建订单失败"
						})
					}
				})
			}
		}
	},
	
	
}	
</script>

